<template>
    <div class="container">
        <div class="card">
            <div class="card-body">
                <h2 class="text-center fw-bold text-info-emphasis"><i class="bi bi-person-workspace me-1"></i>ค่าใช้จ่ายผู้พัฒนาระบบ</h2>
                <form @submit.prevent="calculateCost" class="mb-3">

                    <div class="row mb-1">
                        <div class="col-12 col-md-3"></div>
                        <div class="col-4 col-md-3 my-auto text-end fw-bold"><label
                                class="form-label">จำนวนชั่วโมงที่ใช้&nbsp;:</label>
                        </div>
                        <div class="col-8 col-md-3"><input v-model="hours" type="number" class="form-control"
                                required />
                        </div>
                        <div class="col-12 col-md-3"></div>
                    </div>
                    <div class="row mb-1">
                        <div class="col-12 col-md-3"></div>
                        <div class="col-4 col-md-3 my-auto text-end fw-bold"><label
                                class="form-label">อัตราค่าจ้างต่อชั่วโมง&nbsp;:</label>
                        </div>
                        <div class="col-8 col-md-3"><input v-model="rate" type="number" class="form-control" required />
                        </div>
                        <div class="col-12 col-md-3"></div>
                    </div>
                    <div class="row mb-1">
                        <div class="col-12 col-md-3"></div>
                        <div class="col-4 col-md-3 my-auto text-end fw-bold"><label
                                class="form-label">ค่าใช้จ่ายเพิ่มเติม&nbsp;:</label>
                        </div>
                        <div class="col-8 col-md-3"><input v-model="additionalCosts" type="number" class="form-control"
                                required /></div>
                        <div class="col-12 col-md-3"></div>
                    </div>
                    <div class="row my-2">
                        <div class="col-12">
                            <div class="d-grid gap-1 col-12 col-md-4 mx-auto">
                                <button type="submit" class="btn btn-info"><i class="bi bi-box-arrow-in-down me-1"></i>Setting</button>
                            </div>
                        </div>
                    </div>
                </form>
                <div v-if="totalCost !== null">
                    <div class="row">
                        <div class="col-8 mx-auto alert alert-info">
                            <h3>ค่าใช้จ่าย<br>{{ $root.formatCurrency(totalCost) }}&nbsp;บาท</h3>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-12">
                            <div class="d-grid gap-1 col-12 col-md-4 mx-auto">
                                <button type="submit" class="btn btn-success"
                                    @click="$root.go_to_route('summary')">Next<i class="bi bi-arrow-right-circle ms-1"></i></button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            hours: 1,
            rate: 150,
            additionalCosts: 1000,
            totalCost: null
        };
    },
    methods: {
        calculateCost() {
            var self = this;
            self.totalCost = (self.hours * self.rate) + self.additionalCosts;
            self.$store.commit("set_man_cost", self.totalCost);
        }
    }
};
</script>
